.detail__title h1 {
  color: $color-yell;
  font-family: $font-os-condenced-bold;
  margin-top: 20px;
  text-transform: uppercase;
  font-size: 2em;
}

.detail__price {
  font-size: 1.2em;
  color: $color-yell;
}

.detail__price--big {
  font-size: 1.5em;
  font-family: $font-os-condenced-bold;
  color: $color-green;
}

.detail-image-wrap-global {
  @include box-shadow(3px, 10px, 10px, rgba(0, 0, 0, 0.5));
  background: url('#{$path}/stock-bottom-line.png') top center repeat-x;
}

.detail-hr {
  height:2px;
  border:none;
  color:#efefef;
  background-color:#efefef;
}

.detail-info h1 {
  font-size: 2.5em;
  font-family: $font-os-condenced-bold;
  color: $color-green-dark;
  text-transform: uppercase;
  line-height: 1;
}

.detail-info .subtitle {
  text-transform: uppercase;
  font-size: 0.8em;
  color: #26A69A;
}


.detail-image {
  border-left: 2px solid #efefef;
}


@media (min-width: 961px) {
  #detail .detail-info {
    //background-color: rgba(0, 0, 0, 0.2);
  }
}




