/** Ripples */
/** Elements */
.bottom-sheet-transition-enter {
  transform: translateY(100%);
}
.bottom-sheet-transition-leave-to {
  transform: translateY(100%);
}

.v-bottom-sheet.v-dialog {
  align-self: flex-end;
  border-radius: 0;
  flex: 1 0 100%;
  margin: 0;
  min-width: 100%;
  overflow: visible;
}
.v-bottom-sheet.v-dialog.v-bottom-sheet--inset {
  max-width: 70%;
  min-width: 0;
}
@media only screen and (max-width: 599px) {
  .v-bottom-sheet.v-dialog.v-bottom-sheet--inset {
    max-width: none;
  }
}


